.App {
  background-color: rgb(18, 18, 18);
  width: 100%;
  height: 100%;
  position: fixed;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-weight: 200;
}

p {
  letter-spacing: 7px;
  text-align: justify;
}

.normal-text {
  width: 50%;
  min-width: 380px;
  max-width: 700px;
  margin: auto;
  height: fit-content;
  transition: 2s all ease;
  opacity: 0;
  filter: blur(2px);
}

#options-container {
  width: 50%;
  min-width: 380px;
  max-width: 700px;
  margin: auto;
  height: 75%;
  display: flex;
  flex-direction: column;
}

.options-text {
  display: flex;
  align-items: center;
  width: 100%;
  height: 33.3%;
  padding: 1rem;
  box-shadow: 0.15rem 0.15rem 0px 3px rgb(216, 216, 216);
  cursor: pointer;
  transition: 1s ease all;
  box-sizing: border-box;
  background-color: black;
}

.options-text:hover {
  background-color: rgba(255, 255, 255, 0.123);
}

.hidden {
  opacity: 0;
  filter: blur(2px);
}

.visible {
  opacity: 1;
  filter: blur(0px);
}